.club {
    &__list {
      display: flex;
      flex-direction: column;
      }
  
    &__item {
      margin-bottom: 1rem;
      padding: 1rem;
      width:100%;
      list-style: none;
      border-radius: 1rem;
      border: 1px solid #ddd;
      background-color: #49a46c;
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
      font-weight: bold;
      color: #fff;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
        
       & > * {
         margin: 3px 0;
       }
    }
  
    &__label {
      display: inline-block;
      font-weight: normal;
      //width: 5rem;
      color: #fff;
    }
  
    &__name {
      font-weight: bold;
      font-size: x-large;
      &:hover{
        color:#444;
      }

      
@include smallScreen {
  font-size: small;
}
    }

    &__delete{
      background-color: #49a46c;
      border: solid white;
      border-radius: 2rem;
      color:white;
      font-weight: bolder;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
        &:hover{
        background-color: white;color: #49a46c;
        }
    
  }
  }

  .new-club {
    &__form {
      display: flex;
      flex-direction: column;
      margin-bottom: 1rem;
      padding: 1rem;
      width: 100%;
      list-style: none;
      border-radius: 1rem;
      border: 1px solid #49a46c;
      background-color: #ddd;
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  
    &__title {
      padding-bottom:1rem;
      font-size: 1.5rem;
      color: #49a46c;

      @include smallScreen {
        font-size: small;
      }
    }
  
    &__label{
      font-weight: bold;
      display: flex;
      padding: 0.5rem 0;  
      justify-content: flex-start;
      align-items: center; 
        

       &--input {
       border: 2px solid #ddd;
       border-radius: 0.5rem;
       background-color: #f8f8f8;
       margin: 0.5rem 1rem;
       padding: 3px;
       transition: all ease 0.3s;
          
       @include smallScreen {
        width: 50%;
      }
    }
  
       &:focus {
         border-color: #49a46c;
         background-color: #fff;
         box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
       }
       
     
    }
    
    
  
  
    &__btn {
      padding: 0.5rem 1.5rem;
      width: 100%;
      border-radius: 0.5rem;
      border: 2px solid #49a46c;
      background-color: #49a46c;
      font-weight: bold;
      color: #fff;
      cursor: pointer;
      transition: all ease 0.3s;

        &:hover {
        border-color: #49a46c;
        background-color: #49a46c;
      }
      @include tablet {
        width: 30%;    }
    
    }
  }
  
  