@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
*{margin:0;padding:0;font-family:'Rubik';font-size:1rem;box-sizing:border-box}@media all and (max-width: 320px){*{font-size:0.8rem}}.page{padding:1rem;margin:1rem}.header{display:flex;flex-direction:column;align-items:center;padding-bottom:0.5rem}@media all and (min-width: 768px){.header{flex-direction:row;justify-content:space-between}}.header__title{font-size:2rem;color:#49a46c;padding:0.5rem}.header__search{border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;margin:0.5rem 2rem;padding:3px;transition:all ease 0.3s}.header__search:focus{border-color:#49a46c;background-color:#fff}@media all and (min-width: 768px){.header__search{margin-right:0}}.club__list{display:flex;flex-direction:column}.club__item{margin-bottom:1rem;padding:1rem;width:100%;list-style:none;border-radius:1rem;border:1px solid #ddd;background-color:#49a46c;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:bold;color:#fff;display:flex;flex-direction:row;justify-content:space-between}.club__item>*{margin:3px 0}.club__label{display:inline-block;font-weight:normal;color:#fff}.club__name{font-weight:bold;font-size:x-large}.club__name:hover{color:#444}@media all and (max-width: 320px){.club__name{font-size:small}}.club__delete{background-color:#49a46c;border:solid white;border-radius:2rem;color:white;font-weight:bolder;width:30px;height:30px;display:flex;justify-content:center;align-items:center;cursor:pointer}.club__delete:hover{background-color:white;color:#49a46c}.new-club__form{display:flex;flex-direction:column;margin-bottom:1rem;padding:1rem;width:100%;list-style:none;border-radius:1rem;border:1px solid #49a46c;background-color:#ddd;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-club__title{padding-bottom:1rem;font-size:1.5rem;color:#49a46c}@media all and (max-width: 320px){.new-club__title{font-size:small}}.new-club__label{font-weight:bold;display:flex;padding:0.5rem 0;justify-content:flex-start;align-items:center}.new-club__label--input{border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;margin:0.5rem 1rem;padding:3px;transition:all ease 0.3s}@media all and (max-width: 320px){.new-club__label--input{width:50%}}.new-club__label:focus{border-color:#49a46c;background-color:#fff;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-club__btn{padding:0.5rem 1.5rem;width:100%;border-radius:0.5rem;border:2px solid #49a46c;background-color:#49a46c;font-weight:bold;color:#fff;cursor:pointer;transition:all ease 0.3s}.new-club__btn:hover{border-color:#49a46c;background-color:#49a46c}@media all and (min-width: 768px){.new-club__btn{width:30%}}.copy{font-size:small;display:flex;flex-direction:column;align-items:flex-end}.copy .authorName{display:block}

